import React, { createRef } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as ui_actions from 'src/redux/actions/ui';
import * as word_actions from 'src/redux/actions/word';
import * as domain_actions from 'src/redux/actions/domain';
import { Button, Input } from 'antd';
import { Styled } from './Search.styled';
import SelectTld from 'src/components/Search/SelectTld';
import WordPoss from "../WordPoss";
import PropTypes from "prop-types";
import * as sh from 'universal-common-scripts/functions/sh';

let tld_value = function (props) {
  return Object.keys(props.tlds_user)[0] || Object.keys(props.tlds_checked)[0] || '';
};

const parse_word_and_tld = function (user_input) {
  /*
   * prepare output
   */
  let word = user_input;
  let tld = '';
  /*
   * process input
   */
  // if url contains ".", then assume what comes after is domain extension
  let doti = user_input.indexOf('.');
  if (doti !== -1) {
    // parse tld
    tld = user_input.substr(doti + 1).trim();
    // fix input
    word = user_input.substr(0, doti);
  }
  /*
   * output
   */
  return [word, tld];
};

/*
 * NOTES:
 * 0) page loaded ... parse url
 * 1) url changed ... will any other file be changing the url? if so, it will not contain any variables
 * 2) user changes word ... update state, change url
 * 3) user changes tld ... update state, change url
 */
class Search extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      word: props.word || '',
      tld: tld_value(props)
    };
    /*
     * parse state from url
     */
    let pathlist = this.props.location.pathname.split('/');
    if (pathlist[2]) {
      let [word, tld] = parse_word_and_tld(pathlist[2]);
      // set state in constructor, so that SelectTld component gets correct value the first time!
      this.state.word = word;
      this.state.tld = tld;
      // then, set word/tld the normal way, with side-effects. It will setState, but its ok.
      this.set_word_tld_url(word, tld);
    }
    /*
     * dom manipulation
     */
    this.Input_ref = createRef();
  }

  componentDidMount() {
    /*
     * on-page-load, focus user input
     */
    if (this.Input_ref.current) {
      this.Input_ref.current.focus();
    }
  }

  componentDidUpdate(prevProps) {
    /*
     * when tlds_user or tlds_checked changes,
     * apply first in list as value of SelectTld
     */
    let tld = tld_value(this.props);
    if (tld !== this.state.tld) {
      this.set_word_tld_state(this.state.word, tld);
    }
    /*
     * watch url
     * (/{page_name}/{word}.{tld})
     */
    let pathlist = this.props.location.pathname.split('/');
    let prev_pathlist = prevProps.location.pathname.split('/');
    if (pathlist[2] !== prev_pathlist[2]) {
      this.set_word(pathlist[2]);
    }
  }

  set_word = user_input => {
    if (!user_input) return;
    // if url contains ".", parse TLD from input string
    let word = user_input;
    let doti = user_input.indexOf('.');
    let tld = this.state.tld;
    if (doti !== -1) {
      // parse tld
      tld = user_input.substr(doti + 1).trim();
      // fix input
      word = user_input.substr(0, doti);
    }
    // set
    this.set_word_tld_state(word, tld);
  };

  set_word_tld_state = (word = this.state.word, tld = this.state.tld) => {
    word = word.trim();
    tld = tld.trim();
    /*
     * set url
     */
    this.set_word_tld_url(word, tld);
    /*
     * set state
     */
    this.setState({ word: word, tld: tld });
  };

  set_word_tld_url = (word = this.state.word, tld = this.state.tld) => {
    word = word.trim();
    tld = tld.trim();
    /*
     * redux actions
     */
    this.props.domain_actions.RX__tld_add(tld, 'user');
    this.props.word_actions.RX__word_chunks(word + (tld ? '.' + tld : ''));
    /*
     * set url
     * (/{page_name}/{word}.{tld})
     */
    let pathlist = this.props.location.pathname.split('/');
    pathlist[2] = word;
    if (tld) {
      pathlist[2] += '.' + tld;
    }
    let newpath = pathlist.join('/');
    if (newpath !== this.props.location.pathname) {
      this.props.history.push(newpath);
    }
    /*
     * set title
     */
    window.document.title = word; // + (this.state.tld ? '.' + this.state.tld : '');
  };

  set_state = (key, value) => {
    this.setState({
      [key]: value
    });
  };

  render() {
    let { title, placeholder, cue, hideInput, domains, className, ...props } = this.props;
    return (
      <Styled className={"Search " + className}>
        <div className="content">
          {/*
           * Title
           * If mobile, show only last line (last item in array)!
           */}
          {!!title && (
            <h1 className="title">
            {title}
          </h1>
          )}

          {/*
           * Input Group
           */}
          <div className={'input-group'}>
            {!hideInput && (
              <Input
                ref={this.Input_ref}
                className="Input"
                placeholder={placeholder || 'enter a few words or a phrase...'}
                value={this.state.word}
                onChange={event => {
                  this.set_state('word', event.target.value);
                }}
                onKeyPress={event => {
                  if (event.key === 'Enter') {
                    this.set_word(this.state.word);
                  }
                }}
              />
            )}
            {!!domains && (
              <SelectTld
                value={this.state.tld}
                placeholder=""
                onSelect={value => {
                  this.set_word_tld_state(this.state.word, value);
                }}
              />
            )}
            <Button
              className="Button"
              onClick={() => {
                this.set_word(this.state.word);
              }}
            >
              <span className="hide-small">search &thinsp; </span>
              <i className="fas fa-search"></i>
            </Button>
          </div>

          {/*
           * Cue message
           */}
          {!!cue && (
            <div className="cue hide-small">
              <div className="p">{cue}</div>
            </div>
          )}

          {/*
           * Open POSS
           */}
          <div
            className="show_poss"
            onClick={() => {
              props.ui_actions.RX__toggle_poss();
            }}
          >
            <span>...</span>
          </div>
        </div>

        {/*
         * POSS content, below Search form
         */}
        <WordPoss {...props} />
      </Styled>
    );
  }
}

/*******************************************************************************************************
 * this.props DOCUMENTATION
 *******************************************************************************************************/

const mapDispatchToProps = dispatch => {
  return {
    domain_actions: bindActionCreators(domain_actions, dispatch),
    word_actions: bindActionCreators(word_actions, dispatch),
    ui_actions: bindActionCreators(ui_actions, dispatch),
  };
};

const mapStateToProps = function (state) {
  return {
    word: state.word.word_input,    // string
    tlds_user: state.domain.tlds_user,    // array of tlds
    tlds_checked: state.domain.tlds_checked  // array of tlds
  };
};

Search.propTypes = {
  // passed in from parent, manually:
  domains: PropTypes.bool, // if true, this component will manage .tld in url (if false, will omit .tld from url)
  // passed in from parent, automatically:
  match: PropTypes.object,
  location: PropTypes.object,
  history: PropTypes.object,
};

export default connect(mapStateToProps, mapDispatchToProps)(Search);
