import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { Button, Input } from 'antd';

const InputGroup = props => {
  const { word, pos, api_actions, className = '' } = props;
  console.log({ word, pos, className });
  const [addWords, set_addWords] = useState('');
  return (
    <span className={`flexgrow ui-field-input-group ${className}`}>
      <Input
        type={'text'}
        placeholder={`comma, separated, words, to add to ${pos}...`}
        value={addWords}
        onChange={event => {
          set_addWords(event.target.value);
        }}
        onKeyPress={event => {
          if (event.key === '') {
            api_actions.data_word_remove_words(word, addWords);
            set_addWords('');
          }
          if (event.key === 'Enter') {
            api_actions.data_word_add_poswords(word, pos, addWords);
            set_addWords('');
          }
        }}
      />
      <Button
        type={'primary'}
        onClick={() => {
          api_actions.data_word_add_poswords(word, pos, addWords);
          set_addWords('');
        }}
      >
        <i className={'fas fa-plus'}></i>
      </Button>
      <Button
        className={"color-bad"}
        onClick={() => {
          api_actions.data_word_remove_words(word, addWords);
          set_addWords('');
        }}
      >
        <i className={"fas fa-trash"}></i>
      </Button>
    </span>
  );
};

InputGroup.propTypes = {
  api_actions: PropTypes.object.isRequired, // redux actions
  word: PropTypes.string.isRequired, // `row.key` of word that is being edited
  pos: PropTypes.string.isRequired, // part of speech. ex: "nouns"
};

export default InputGroup;
